.video-section {

  &__header {
    display: flex;
    align-items: center;
    gap: 60px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-direction: column;
      justify-content: center;
      gap: 16px;
    }
  }

  &__link-all {
    min-width: 150px;
    padding-top: 6px;
    padding-bottom: 6px;
    border-radius: 0;

    @include mobile {
      min-width: auto;
      width: 100%;
      max-width: 360px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;

    @include mobile {
      flex-direction: column;
      gap: 16px;
    }
  }

  &__item {
    width: calc(50% - 10px);

    @include mobile {
      width: 100%;
    }
  }
}


