.feedback {
  padding-top: 70px;

  &__container {
    align-items: center;
    padding: 50px 40px;

    @media (max-width: 1400px) {
      border-radius: 30px;
    }

    @include tablet {
      padding: 35px 20px;
    }

    @include small-tablet {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 20px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--light-color);

    @include small-desktop {
      font-size: 28px;
    }

    @include tablet {
      text-align: center;
    }
    @include mobile {
      margin-bottom: 8px;
      font-size: 26px;
    }
  }

  &__desc {
    margin: 0;
    max-width: 455px;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);

    @include tablet {
      text-align: center;
    }
    @include mobile {
      font-size: 14px;
    }
  }

  &__form-content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    & .form-field__input {
      border-color: var(--light-color);
      min-width: 261px;
      color: var(--light-color);
      background-color: transparent;

      &::placeholder {
        color: var(--gray-wild-color);
      }
      &:focus {
        outline: none;
        border-color: var(--orange-color);
      }

      @include hover {
        border-color: var(--orange-color);
      }
    }
    & .form__btn {
      align-self: stretch;

      @include small-tablet {
        align-self: center;
      }
    }
    & .form-argee {
      max-width: 455px;
    }
    & .form-argee label span {
      color: var(--light-color);
    }
    & .form-argee label span::before {
      border-color: var(--light-color);
      background-color: transparent;
    }
  }

  &__row {
    display: flex;
    gap: 26px;

    @include small-desktop {
      flex-direction: column;
    }

  }

  &__img {
    position: relative;
    margin: 0 auto;
    width: 470px;
    height: 320px;

    @include small-desktop {
      height: 342px;
    }

    @include small-tablet {
      display: none;
    }

    & img {
      position: absolute;
      bottom: -50px;

      @include tablet {
        bottom: -35px;
      }
    }
  }

}
