.card-product {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  border: 1px solid var(--gray-gallery-color);
  padding: 20px 14px;
  max-width: 100%;
  height: 100%;
  min-height: 180px;
  transition: box-shadow .4s ease-in-out;

  &:focus {
    outline: none;
    // box-shadow: 8px 8px 4px 0 rgba(168, 168, 168, .25);
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, 0.25), 0 4px 10px 0 rgba(104, 104, 104, 0.25);

  }
  @include hover {
    // box-shadow: 8px 8px 4px 0 rgba(168, 168, 168, .25);
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, 0.25), 0 4px 10px 0 rgba(104, 104, 104, 0.25);

  }

  &__indicator {
    font-size: 14px;
  }

  & > a {
    display: block;
    width: 100%;
    cursor: pointer;

    &:focus {
      outline: none;
      & img {
        transform: scale(1.1);
      }
    }
    @include hover {
      & img {
        transform: scale(1.1);
      }
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

    & img {
      height: auto;
      max-height: 190px;
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, 0.2));
      object-fit: contain;
      transition: transform .3s ease-in-out;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    height: 100%;
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    color: var(--dark-text-color);

    & a {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      cursor: pointer;
      transition: color .3s ease-in-out;

      &:focus {
        outline: none;
        color: var(--orange-color);
      }
      @include hover {
        color: var(--orange-color);
      }
      &:active {
        color: var(--orange-color);
      }
    }

  }

  &__param {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--dark-text-color);
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
    margin-top: auto;
  }

  &__badge {
    border: 1px solid var(--orange-color);
    padding: 6px 14px;
    // width: calc(40% - 5px);
    min-width: 70px;
    font-weight: 400;
    font-size: 14px;
    white-space: nowrap;
    text-align: center;
    text-transform: uppercase;
    color: var(--red-color);
  }

  &__price-byn {
    display: flex;
    flex-direction: column;
    align-items: center;
    // max-width: 140px;
    width: 50%;
    margin-left: auto;

    & .price__value {
      font-weight: 400;
      font-size: 16px;
      color: var(--dark-text-color);
    }
  }

  &__footer {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    padding-top: 10px;
  }

  &__price {
    width: calc(55% - 5px);
  }

  &__btn {
    border-radius: 0;
    padding: 8px 12px;
    width: calc(45% - 5px);
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    text-align: center;
    color: var(--light-color);
    // background: var(--gradient-orange);
    background: var(--orange-color);
    transition: filter 0.4s ease-in-out;
  }
}

.price {
  display: flex;
  flex-direction: column;
  gap: 4px;

  &__value {
    font-weight: 600;
    font-size: 18px;
    line-height: 1;
    // color: var(--dark-text-color);
    color: var(--blue-color);
  }

  &__note {
    font-weight: 400;
    font-size: 10px;
    line-height: 1;
    text-align: center;
    color: var(--dark-text-color);
  }

  &__old {
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    text-decoration: line-through;
    color: var(--dark-text-color);
  }
}
