.warehouse-section {

  &__header {
    display: flex;
    align-items: center;
    gap: 60px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-direction: column;
      justify-content: center;
      gap: 16px;
    }
  }


  &__link-all {
    min-width: 150px;
    padding-top: 6px;
    padding-bottom: 6px;
    border-radius: 0;
    background-color:#19bb3c;

    @include mobile {
      min-width: auto;
      width: 100%;
      max-width: 360px;
    }
  }

  &__slider {
    padding: 10px;
    margin: -10px;

    & .swiper-slide {
      display: flex;
    }
    & .card-product {
      width: 100%;
      height: auto;
    }
    // & .card-product__img img {
    //   max-height: 170px;
    // }
  }

  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;

    @include mobile {
      margin-top: 24px;
    }

    & .swiper-pagination-bullet {
      display: inline-block;
      margin: 0 4px;
      border-radius: 2px;
      width: 54px;
      height: 8px;
      background-color: var(--blue-light-color);
      cursor: pointer;

      @include mobile {
        width: 24px;
        height: 4px;
      }

    }
  }
}

.warehouse-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 16px;
  width: 100%;
  text-align: center;
  transition: box-shadow .4s ease-in-out;

  @include hover {
    & .warehouse-card__img {
      transform: scale(1);
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    }
  }

  @include mobile {
    border-radius: 12px;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    border-radius: inherit;
    transform: scale(0.95);
    transition: transform .4s ease-in-out, box-shadow .4s ease-in-out;

    & img {
      border-radius: inherit;
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
}

