.news-section {
  &__header {
    display: flex;
    align-items: center;
    gap: 60px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-direction: column;
      justify-content: center;
      gap: 16px;
    }
  }

  &__link-all {
    min-width: 150px;
    padding-top: 6px;
    padding-bottom: 6px;
    border-radius: 0;

    @include mobile {
      min-width: auto;
      width: 100%;
      max-width: 360px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    width: 100%;
  }

  &__item {
    width: calc(100%/3 - 20px*2/3);
    min-height: 380px;

    @include small-desktop {
      width: calc(100%/2 - 20px*1/2);
    }

    @include mobile {
      width: 100%;
    }

    &:nth-child(4n+1) {
      & .news-card {
        color: var(--light-color);
        background-color: var(--blue-color);
      }
    }
    &:nth-child(4n+3) {
      & .news-card {
        color: var(--light-color);
        background-color: var(--dark-text-color);
      }
    }

  }
}
