}
.map__p:focus::before {
  filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
}
.map__p:focus .map__text {
  opacity: 1;
  visibility: visible;
}
@media (any-hover: hover) {
  .map__p:hover {
    z-index: 200;
  }
  .map__p:hover::before {
    filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
  }
  .map__p:hover .map__text {
    opacity: 1;
    visibility: visible;
  }
}
.map__p:active {
  z-index: 200;
}
.map__p:active::before {
  filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
}
