}
@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 194, 255, 0.6666666667);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 20px transparent;
