.oursites-section__content{
  display: flex;
  flex-wrap: wrap;
  gap: 52px;
  justify-content: center;
}
@media (max-width: 768px) {
  .oursites-section__content {
    gap: 16px;
  }
}
.oursites-section__content__block {
  display: flex;
  flex-direction: column;
}
.oursites-section__content__block__link {
  padding-top: 18.21px;
  webkit-transition: color 0.4s ease-in-out;
      transition: color 0.4s ease-in-out;
}
.oursites-section__content__block a {
  margin: 0;
  max-width: 388px;
  font-family: var(--title-family);
  font-weight: 600;
  font-size: 28px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: var(--dark-text-color);
 }
@media (max-width: 1024px) {
  .oursites-section__content__block__link {
    padding-top: 10px;
  }
}

.oursites-section__content__block a {
  margin: 0;
  max-width: 388px;
  font-family: var(--title-family);
  font-weight: 600;
  font-size: 28px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: var(--dark-text-color);
}
@media (max-width: 1200px) {
  .oursites-section__content__block a {
    font-size: 26px;
  }
}
@media (max-width: 1024px) {
  .oursites-section__content__block a {
    font-size: 24px;
  }
}

.oursites-section__content__block img{
  -webkit-transition: transform  0.5s ease-in-out;
    transition: transform  0.5s ease-in-out;
  display: block;
  width: 100%;
  box-shadow: 0px -1px 4px 0px rgba(0, 0, 0, 0.25),0px 3px 4px 0px rgba(0, 0, 0, 0.25);
}
.oursites-section__content__block img:hover {
  transform: scale(1.1);

}
.oursites-section__content__block:hover .oursites-section__content__block__link {
  color: var(--orange-color);
}
