.form-field {

  &__input {
    border: 1px solid var(--dark-text-color);
    border-radius: 10px;
    padding: 16px;
    width: 100%;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark-text-color);
    transition: border-color 0.4s ease-in-out;

    @include small-desktop {
      font-size: 14px;
      line-height: 130%;
    }

    @include tablet {
      padding: 12px 16px;
    }

    &--big {
      font-family: var(--font-family);
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      max-height: 115px;
      resize: none;

      @include small-tablet {
        min-height: 80px;
      }
    }

    &:focus {
      outline: none;
      border-color: var(--orange-color);
    }

    @include hover {
      border-color: var(--orange-color);
    }

    &::placeholder {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--gray-text-color);

      @include big-desktop {
        font-size: 14px;
      }
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {
      border-color: var(--orange-color);
      -webkit-box-shadow: 0 0 0px 40rem #f5f5f5 inset;
    }
  }
}
