.service {

  &__container {
    position: relative;
    align-items: center;
    justify-content: flex-end;
    gap: 30px;
    overflow: hidden;

    @include small-desktop {
      padding: 40px 30px;
    }
    @include tablet {
      flex-direction: column-reverse;
      gap: 8px;
    }
    @include mobile {
      padding: 24px 16px;
    }
  }

  &__img {
    flex-shrink: 0;
    display: block;
    max-width: 700px;
    font-size: 0;

    @include small-desktop {
      max-width: 600px;
    }

    @include tablet {
      flex-shrink: 1;
      margin-top: -40px;
    }

    @include mobile {
      display: none;
    }

    & img {
      width: 100%;
      max-height: 530px;
      object-fit: cover;
      transform: translateY(40px);

      @media (max-width: (1400px)) {
        max-height: 38vw;
      }
      @include small-desktop {
        max-height: 44vw;
        transform: translateY(50px);
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-right: 30px;
    width: 45%;

    @include small-desktop {
      padding: 0;
      width: 100%;
    }
    @include tablet {
      width: 100%;
      max-width: 80%;
    }
    @include mobile {
      max-width: none;
    }
  }

  &__form {
    width: 100%;
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--light-color);

    @include small-desktop {
      font-size: 28px;
    }

    @include tablet {
      text-align: center;
    }
    @include mobile {
      margin-bottom: 8px;
      font-size: 26px;
    }
  }

  &__desc {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);

    @include tablet {
      text-align: center;
    }
    @include mobile {
      font-size: 14px;
    }
  }

  &__form-content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include mobile {
      gap: 16px;
    }

    & .form-field__input {
      border-color: var(--light-color);
      color: var(--light-color);
      background-color: transparent;

      &::placeholder {
        color: var(--gray-wild-color);
      }
      &:focus {
        outline: none;
        border-color: var(--orange-color);
      }

      @include hover {
        border-color: var(--orange-color);
      }
    }

    & .form-argee label span {
      color: var(--light-color);
    }
    & .form-argee label span::before {
      border-color: var(--light-color);
      background-color: transparent;
    }
  }
}
