.form-argee {

  & label {
    position: relative;
    display: flex;
    align-items: center;
    gap: 6px;
    cursor: pointer;

    & span {
      position: relative;
      padding-left: 22px;
      font-weight: 400;
      font-size: 12px;
      line-height: 117%;
      color: var(--dark-text-color);

      & a {
        position: relative;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        color: inherit;
        text-decoration: underline;
        text-decoration-thickness: 0.5px;
        text-underline-offset: 3px;
        transition: color .4s ease-in-out;

        &:focus {
          outline: none;
          color: var(--orange-color);
        }
        @include hover {
          color: var(--orange-color);
        }
      }
    }

    & span::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      flex-shrink: 0;
      display: block;
      margin-right: 8px;
      border: 1px solid var(--dark-text-color);
      border-radius: 4px;
      width: 16px;
      height: 16px;
      background-color: var(--light-color);
      transition: border-color 0.4s ease-in-out;
      transform: translateY(-50%);
      cursor: pointer;
    }

    & span::after {
      content: "";
      position: absolute;
      top: calc(50% - 1.5px);
      left: 5.5px;
      border-left: 3px solid var(--orange-color);
      border-bottom: 3px solid var(--orange-color);
      border-radius: 0.5px;
      width: 9px;
      height: 5px;
      opacity: 0;
      visibility: hidden;
      transform: rotate(-45deg) translateY(-50%);
      transition: opacity 0.4s ease-in-out, visible 0.4s ease-in-out;
    }

    & input[type=checkbox]:focus + span::before {
      border-color: var(--orange-color);
    }
    & input[type=checkbox]:checked + span::after {
      opacity: 1;
      visibility: visible;
    }
  }
  & input[type=checkbox], &__input {
    position: absolute;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
  }

  & .wpcf7-list-item {
    display: block;
    margin: 0;
  }
}
