.about-section {

  &__title {}

  &__content {
    display: flex;
    gap: 20px;
    border-radius: 16px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__descr {
    padding: 30px 20px;
    border-radius: inherit;
    width: calc(50% - 10px);
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: left;
    // color: var(--dark-text-color);
    // background-color: var(--gray-wild-color);
    color: var(--light-color);
    // background-color: var(--blue-color);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../img/support-bg.jpg);

    @include tablet {
      width: 100%;
    }

    & p {
      margin: 0;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      text-align: inherit;
      color: inherit;
    }
  }

  &__videoblock {
    border-radius: inherit;
    width: calc(50% - 10px);

    @include tablet {
      width: 100%;
    }
  }
}
.videoblock {
  position: relative;
  overflow: hidden;

  &__video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__btn-play {}
}
