.news-card {
  display: flex;
  flex-direction: column;
  height: 100%;
  color: var(--dark-text-color);
  background-color: var(--gray-wild-color);
  transition: box-shadow .4s ease-in-out;

  @include hover {
    box-shadow: 0 4px 6px rgba(0, 0, 0, .3);

    & .news-card__img img {
      transform: scale(1.1);
    }
    & .news-card__title::after {
      transform: translateX(10px) skewX(20deg);
    }
    & .news-card__link {
      color: var(--orange-color);
    }

  }

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    min-height: 180px;
    font-size: 0;
    overflow: hidden;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform .4s ease-in-out;
    }
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    padding: 60px 20px 20px;
    height: 100%;
  }

  &__title {
    position: absolute;
    z-index: 1;
    top: 1px;
    left: 0;
    display: flex;
    align-items: center;
    margin: 0;
    border-top-right-radius: 16px;
    padding: 8px 20px;
    width: 70%;
    min-height: 60px;
    font-family: var(--title-family);
    font-weight: 500;
    font-size: 22px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--light-color);
    background-color: var(--blue-light-color);
    transform: translateY(-50%);

    @include mobile {
      font-size: 20px;
    }

    & span {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      -webkit-box-orient: vertical;
      display: block;
      display: -webkit-box;
      overflow: hidden !important;
      text-overflow: ellipsis;
      -webkit-line-clamp: 4;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -20px;
      z-index: -1;
      border-radius: 16px;
      width: 70px;
      height: 100%;
      transform: skewX(20deg);
      background-color: var(--blue-light-color);
      transition: transform .4s ease-in-out;
    }
  }

  &__excerpt {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: inherit;
    transition: color 0.4s ease;

    & p {
      margin: 0;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      -webkit-box-orient: vertical;
      display: block;
      display: -webkit-box;
      overflow: hidden !important;
      text-overflow: ellipsis;
      -webkit-line-clamp: 4;

      @include mobile {
        font-size: 14px;
      }
    }
  }

  &__link {
    align-self: end;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: inherit;
    text-decoration: underline;
    text-underline-offset: 5px;
    transition: color 0.4s ease, text-decoration 0.4s ease;

    @include mobile {
      font-size: 14px;
    }
  }
}
