.machine-table__wrapper .tablepress {
	--head-text-color: #fff;
	--head-bg-color: linear-gradient(166deg,#122541 0%,#183f78 100%);
	--hover-text-color: #fff;
	--hover-bg-color: #363636;
	--odd-text-color: #363636;
	--odd-bg-color: #f5f5f5;
	--even-text-color: #363636;
	--even-bg-color: #f5f5f5;
	--border-color: #fff;
	margin: 0;
	border-style: solid;
	border-spacing: 1.5px;
	border-color: var(--border-color);
	border-collapse: separate;
	width: 100%;
}

.machine-table__wrapper caption {
	display: none;
}

.machine-table__wrapper thead {
	font-weight: 700;
	color: var(--head-text-color);
	background: var(--head-bg-color);
}

.machine-table__wrapper tr {
	font-size: 12px;
	line-height: 1.2;
}

.machine-table__wrapper tbody tr:nth-child(even) {
	font-weight: 400;
	color: var(--even-text-color);
	background-color: var(--even-bg-color);
	-webkit-transform: background-color .3s ease-in-out, color .3s ease-in-out;
	-ms-transform: background-color .3s ease-in-out, color .3s ease-in-out;
	transform: background-color .3s ease-in-out, color .3s ease-in-out;
}

@media (any-hover: hover) {
	.machine-table__wrapper tbody tr:nth-child(even):hover {
		color: var(--hover-text-color);
		background-color: var(--hover-bg-color);
	}
}

.machine-table__wrapper tbody tr:nth-child(odd) {
	font-weight: 400;
	color: var(--odd-text-color);
	background-color: var(--odd-bg-color);
	-webkit-transform: background-color .3s ease-in-out, color .3s ease-in-out;
	-ms-transform: background-color .3s ease-in-out, color .3s ease-in-out;
	transform: background-color .3s ease-in-out, color .3s ease-in-out;
}

@media (any-hover: hover) {
	.machine-table__wrapper tbody tr:nth-child(odd):hover {
		color: var(--hover-text-color);
		background-color: var(--hover-bg-color);
	}
}

.machine-table__wrapper th,
.machine-table__wrapper td {
	padding: 14px 10px;
	text-align: center;
	font-weight: inherit;
	font-size: inherit;
	line-height: inherit;
}

.bandsaws-modify-card__table .tablepress,
.bandsaws-post-card__table .tablepress {
  --head-text-color: #363636;
  --head-bg-color: #f5f5f5;
  --hover-text-color: #fff;
  --hover-bg-color: #909090;
  --odd-text-color: #363636;
  --odd-bg-color: #fff;
  --even-text-color: #363636;
  --even-bg-color: #fff;
  --border-color: #fff;
  margin: 0;
  border-style: solid;
  border-spacing: 1.5px;
  border-color: var(--border-color);
  border-collapse: separate;
  width: 100%;
  font-weight: 400;
  font-size: 12px;
}

.bandsaws-modify-card__table caption,
.bandsaws-post-card__table caption {
  display: none;
}

.bandsaws-modify-card__table thead,
.bandsaws-post-card__table thead {
  font-weight: inherit;
  font-size: inherit;
  color: var(--head-text-color);
  background: var(--head-bg-color);
}

.bandsaws-modify-card__table th,
.bandsaws-modify-card__table td,
.bandsaws-post-card__table th,
.bandsaws-post-card__table td {
  padding: 12px 16px;
  font-weight: inherit;
  font-size: inherit;
  text-align: center;
  color: inherit;
  background: inherit;
}

@media (max-width: 576px) {
  .bandsaws-modify-card__table th,
  .bandsaws-modify-card__table td,
  .bandsaws-post-card__table th,
  .bandsaws-post-card__table td {
    padding: 12px 8px;
  }
}

.bandsaws-modify-card__table tr,
.bandsaws-post-card__table tr {
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
}

.bandsaws-modify-card__table tr:nth-child(1) td.column-1,
.bandsaws-modify-card__table tr:last-child td,
.bandsaws-post-card__table tr:nth-child(1) td.column-1,
.bandsaws-post-card__table tr:last-child td {
  background-color: var(--head-bg-color);
}

.bandsaws-modify-card__table tr:first-child td:not(.column-1),
.bandsaws-post-card__table tr:first-child td:not(.column-1) {
  color: #fff;
  background-color: #2f5894;
}

.bandsaws-modify-card__table tr:not(:last-child):not(:first-child) td:not(.column-1),
.bandsaws-post-card__table tr:not(:last-child):not(:first-child) td:not(.column-1) {
  color: #fff;
  background-color: #fb8b56;
}

@media (any-hover: hover) {
  .bandsaws-modify-card__table tbody tr:nth-child(odd):hover,
  .bandsaws-post-card__table tbody tr:nth-child(odd):hover {
    color: var(--hover-text-color);
    background-color: var(--hover-bg-color);
  }

  .bandsaws-modify-card__table tbody tr:nth-child(even):hover,
  .bandsaws-post-card__table tbody tr:nth-child(even):hover {
    color: var(--hover-text-color);
    background-color: var(--hover-bg-color);
  }

  .bandsaws-modify-card__table tr:nth-child(1):hover td.column-1,
  .bandsaws-modify-card__table tr:last-child:hover td,
  .bandsaws-post-card__table tr:nth-child(1):hover td.column-1,
  .bandsaws-post-card__table tr:last-child:hover td {
    color: var(--hover-text-color);
    background-color: var(--hover-bg-color);
  }

  .bandsaws-modify-card__table tr:first-child:hover td:not(.column-1),
  .bandsaws-post-card__table tr:first-child:hover td:not(.column-1) {
    color: var(--hover-text-color);
    background-color: var(--hover-bg-color);
  }

  .bandsaws-modify-card__table tr:not(:last-child):not(:first-child):hover td:not(.column-1),
  .bandsaws-post-card__table tr:not(:last-child):not(:first-child):hover td:not(.column-1) {
    color: var(--hover-text-color);
    background-color: var(--hover-bg-color);
  }
}
