.equipment-section {

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    width: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100%/3 - 20px*2/3);
    min-height: 413px;


    @include tablet {
      width: calc(100%/2 - 20px*1/2);
    }

    @include small-tablet {
      min-height: 320px;
    }
    @include mobile {
      width: 100%;
      min-height: auto;
    }

    &:first-child {
      width: calc((100%/3)*2 - 20px*1/3);

      @include tablet {
        width: calc(100%/2 - 20px*1/2);
      }

      @include mobile {
        width: 100%;
      }

      & .equipment-section__link {
        flex-direction: row-reverse;
        justify-content: center;

        @include tablet {
          flex-direction: column;
          justify-content: flex-end;
        }
      }
      & .equipment-section-card__img {
        max-width: 400px;
      }
    }
    &:nth-child(6n+7) {
      width: calc((100%/3)*2 - 20px*1/3);

      @include tablet {
        width: 100%;;
      }

      & .equipment-section__link {
        flex-direction: row;
        justify-content: center;

        @include mobile {
          flex-direction: column;
          justify-content: flex-end;
        }
      }
      & .equipment-section-card__img {
        max-width: 400px;
      }
    }

    &:nth-child(6n+8) {
      width: 100%;

      & .equipment-section__link {
        flex-direction: row-reverse;
        justify-content: center;

        @include mobile {
          flex-direction: column;
          justify-content: flex-end;
        }
      }
      & .equipment-section-card__img {
        max-width: 600px;
        min-height: 340px;

        @include mobile {
          max-width: none;
          min-height: 190px;
        }

        & img {
          max-width: 600px;
          max-height: 340px;

          @include mobile {
            max-width: 400px;
            max-height: 300px;
          }
        }
      }
    }
  }

  &__link {}

}

.equipment-section-card {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  gap: 8px;
  padding: 20px 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: filter .4s ease-in-out;

  @include small-tablet {
    justify-content: space-between;
    padding: 20px 0 30px;
  }

  @include hover {
    filter: brightness(1.07) drop-shadow(0 0 9px rgba(255, 255, 255, .8));

    & .equipment-section-card__img img {
      transform: scale(1.1);
    }
    & .equipment-section-card__icon {
      stroke: var(--orange-color);
      fill: var(--orange-color);
      transform: translateX(8px);
    }
  }

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background: linear-gradient(136deg, rgba(236, 236, 236, 0.9) 0%, rgba(224, 224, 224, 0.9) 100%);
  }

  &__img {
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 300px;
    transition: transform .4s ease-in-out;

    @include small-tablet {
      min-height: 190px;
    }

    & img {
      width: 100%;
      max-width: 400px;
      height: auto;
      max-height: 300px;
      object-fit: cover;
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, 0.4));
      transition: transform .4s ease-in-out;
    }
  }

  &__title {
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    margin: 0;
    min-height: 52px;
    max-width: 300px;
    font-family: var(--title-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 1;
    letter-spacing: 1px;
    text-align: center;
    text-transform: uppercase;
    color: var(--dark-text-color);

    @include small-desktop {
      font-size: 26px;
    }
    @include tablet {
      font-size: 24px;
    }
    @include small-mobile {
      max-width: 85%;
    }

  }

  &__icon {
    position: absolute;
    z-index: 3;
    bottom: 20px;
    right: 28px;
    width: 32px;
    height: 16px;
    stroke-width: 2px;
    stroke: var(--blue-light-color);
    fill: var(--blue-light-color);
    transition: fill .4s ease, stroke .4s ease-in-out, transform .4s ease-in-out;

    @include small-tablet {
      bottom: 12px;
      right: 18px;
      width: 26px;
      height: 13px;
    }
  }

}

