.numbers {
  padding-top: 40px;

  @include mobile {
    padding-top: 20px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;

    @include mobile {
      align-items: center;
      justify-content: center;
      gap: 40px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: calc(100%/4 - 20px*3/4);

    @include tablet {
      width: calc(100%/2 - 20px*1/2);
    }

    @include mobile {
      gap: 8px;
      width: 100%;
    }
  }

  &__top {
    position: relative;
    display: flex;
    font-family: var(--title-family);
    font-weight: 700;
    // font-size: 48px;
    font-size: 78px;
    line-height: 46%;
    color: var(--blue-cronflower-color);

    @include mobile {
      font-size: 68px;
    }
  }

  &__count {
    position: absolute;
    top: 0;
    left: 0;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    text-align: right;
    color: inherit;
  }

  &__unit {
    padding-left:calc(34px*var(--index));
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;

    @include mobile {
      padding-left:calc(28px*var(--index));
    }

    // .numbers__item:nth-child(1) &, .numbers__item:nth-child(4) & {
    //   padding-left: calc(65px + 8px);

    //   @include mobile {
    //     padding-left: 52px;
    //   }
    // }
    // .numbers__item:nth-child(2) &, .numbers__item:nth-child(3) & {
    //   padding-left: calc(130px + 8px);
    //   @include mobile {
    //     padding-left: 104px;
    //   }
    // }
  }

  &__descr {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 700;
    // font-size: 20px;
    font-size: 30px;
    line-height: 100%;
    // line-height: 125%;
    letter-spacing: .02em;
    text-align: center;
    color: var(--blue-cronflower-color);
  }
}
