.bandsaws-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  height: 100%;
  cursor: pointer;

  @include small-tablet {
    gap: 16px;
  }

  @include hover {
    & .bandsaws-card__img {
      border-color: var(--orange-color);
    }
    & .bandsaws-card__img img {
      transform: translate(-50%, -50%) scale(1.05);
    }
    & .bandsaws-card__title {
      color: var(--orange-color);
    }
  }


  &__img {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 3px solid var(--blue-color);
    border-radius: 100%;
    padding-bottom: 100%;
    width: 100%;
    height: 0;
    transition: border-color .4s ease-in-out;

    & img {
      position: absolute;
      top: 50%;
      left: 50%;
      padding: 4%;
      width: 100%;
      transform: translate(-50%, -50%);
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, 0.4));
      transition: transform .4s ease-in-out;
    }
  }

  &__title {
    margin: 0;
    max-width: 300px;
    font-family: var(--title-family);
    font-weight: 600;
    font-size: 28px;
    line-height: 120%;
    text-align: center;
    text-transform: uppercase;
    color: var(--dark-text-color);
    transition: color .4s ease-in-out;

    @include small-desktop {
      font-size: 26px;
    }
    @include tablet {
      font-size: 24px;
    }
    @include small-tablet {
      max-width: 200px;
    }
    @include mobile {
      max-width: 100%;
    }

  }
}
