.form, form {

  &__btn {
    align-self: center;
    width: 100%;
    max-width: 280px;
  }
  & .wpcf7-spinner {
    display: none;
    position: absolute;
    bottom: 35px;
    right: 25px;
  }
  & .wpcf7-response-output {
    display: none;
  }
}

.form-container-bg {
  position: relative;
  display: flex;
  align-items: flex-start;
  border-radius: 30px;
  padding: 40px;
  margin: 0 auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #636363;


  @include mobile {
    border-radius: 20px;
    padding: 24px 16px;
  }
}
