.support {
  padding-top: 60px;

  @include small-desktop {
    padding-top: 0;
  }

  &__container {
    justify-content: space-between;


    @include tablet {
      flex-direction: column;
      align-items: flex-end;
      justify-content: flex-start;
      gap: 20px;
    }

    @include small-tablet {
      align-items: center;
      gap: 30px;
    }

    @include mobile {
      gap: 16px;
      padding: 24px 16px;
    }
  }

  &__info {
    max-width: 32%;

    @include tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 100vw;
      width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--light-color);

    @include small-desktop {
      font-size: 28px;
    }

    @include tablet {
      text-align: center;
    }
    @include mobile {
      margin-bottom: 8px;
      font-size: 26px;
    }
  }

  &__desc {
    position: relative;
    z-index: 2;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);

    @include tablet {
      text-align: center;
    }
    @include mobile {
      font-size: 14px;
    }

    & p {
      margin: 0;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 45%;
    font-size: 0;
    transform: translateX(-50%);

    @include small-desktop {
      left: 47%;
    }

    @include tablet {
      bottom: 0;
      left: -20px;
      transform: translateX(0%);
    }

    @include small-tablet {
      display: none;
    }

    & img {
      max-width: 420px;
      height: auto;
      object-fit: cover;

      @include small-desktop {
        max-width: 385px;
      }
      @include tablet {
        max-width: 50vw;
      }
      @include mobile {
        padding: 24px 16px;
      }
    }
  }

  &__form {
    position: relative;
    z-index: 2;
    margin-right: 20px;
    max-width: 36%;

    @include tablet {
      padding: 60px 0;
      max-width: 50vw;
    }
    @include small-tablet {
      margin-right: 0;
      padding: 0;
      width: 100%;
      max-width: none;
    }
  }

  &__form-content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include mobile {
      gap: 16px;
    }

    & .form-field__input {
      border-color: var(--light-color);
      color: var(--light-color);
      background-color: transparent;

      &::placeholder {
        color: var(--gray-wild-color);
      }
      &:focus {
        outline: none;
        border-color: var(--orange-color);
      }

      @include hover {
        border-color: var(--orange-color);
      }
    }

    & .form-argee label span {
      color: var(--light-color);
    }
    & .form-argee label span::before {
      border-color: var(--light-color);
      background-color: transparent;
    }
  }

}
