.bandsaws-section {
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    @include mobile {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 16px;
    }
  }

  &__item {
    max-width: 300px;
    width: calc(100%/3 - 20px*2/3);

    @include mobile {
      width: 100%;
      max-width: 300px;
    }
  }
}
