.promo {
  @include mobile {
    margin-bottom: 60px;
  }

  &__slider {
    position: relative;
    width: 100vw;
    height: calc(100vw*3/10);
    max-height: 600px;

    @include small-tablet {
      height: calc(var(--vh) - var(--header-height));
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 0;

    @include small-tablet {
      height: 100%;
    }

    & img {
      width: 100%;
      max-width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__pagination {
    position: absolute;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;

    & .swiper-pagination-bullet {
      position: relative;
      overflow: hidden;
      display: inline-block;
      margin: 0 4px;
      border-radius: 2px;
      width: 54px;
      height: 6px;
      opacity: 1;
      background-color: rgba(86, 165, 255, 0.3);
      cursor: pointer;

      transition: opacity .3s, height .3s;

      @include mobile {
        width: 24px;
        height: 6px;
      }
    }
    & .swiper-pagination-bullet::before {
      content: '';
      position: absolute;
      inset: 0;
      display: block;
    }
    & .swiper-pagination-bullet-active {
      transition: opacity .3s, width .3s;
    }
    .swiper-pagination-bullet-active::before {
        background-color: var(--blue-light-color);
        animation: slide-progress 4.5s linear forwards;
      }
      @keyframes slide-progress {
        0% {
          transform: translateX(-100%);
        }

        100% {
          transform: translateX(0);
        }
      }
  }

  &__sideform {
    position: fixed;
    right: 0;
    bottom: 15%;
    top: calc(var(--header-height) + 80px);
    z-index: 30;
    border-radius: 36px 8px 8px 8px;
    padding: 20px 40px;
    width: 550px;
    height: 262px;
    background: linear-gradient( #469bff 0%, #153059 100%);
    backdrop-filter: blur(15px);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transform: translateX(93%);
    transition: transform .4s ease-in-out;

    @include tablet {
      top: calc(var(--header-height) + (100vw*9/16 - 400px)/2);
    }

    @include small-tablet {
      // top: calc(var(--header-height) + (100vw*9/16 - 347px)/2);
      padding: 12px 20px;
      width: 96vw;
      max-width: 360px;
      transform: translateX(90%);
    }
    @include mobile {
      top: calc(var(--header-height) + 10px);
    }
    & span {
      color: var(--light-color) !important;
    }

    & .sideform {
      opacity: 0;
    }

    &.is-visible {
      height: fit-content;
      // height: auto;
      transform: translateX(0);
      transition: transform 0.4s ease-in-out;

      & .sideform {
        opacity: 1;
      }

      & .promo__btn-sideform span{
        opacity: 0;
      }
    }

  }

  &__btn-sideform {
    position: absolute;
    top: -1px;
    left: 1px;
    z-index: 200;
    display: block;
    // border-top-left-radius: 8px;
    // border-bottom-left-radius: 8px;
    border-radius: 36px 8px 8px 8px;
    width: 42px;
    height: 100%;
    font-size: 0;
    background-color: transparent;
    cursor: pointer;

    & span {
      display: inline-flex;
      position: relative;
      justify-content: center;
      left: -82px;
      align-items: center;
      width: 200px;
      font-family: var(--title-family);
      font-size: 24px;
      letter-spacing: 1px;
      transform: rotate(-90deg);
      transform-origin: center;
    }
    &::before {
      rotate: -90deg;
      top: 0px;
      left: 2px;
      content: '';
      position: absolute;
      width: 32px;
      height: 34px;
      background-image: url('../img/promo_btn.svg');
      background-size: cover;
  }
  }
}
.swiper-wrapper {
  align-items: stretch;
}
.swiper-slide {
  position: relative;
  height: auto;

  &__link {
    position: absolute;
    bottom: 45px;
    left: 11vw;
    border-radius: 0;
    padding: 8px 16px;
    min-width: 150px;
    font-weight: 700;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: 0.02em;
    text-align: center;
    color: var(--light-color);
    background: var(--gradient-orange);
    transition: filter 0.4s ease-in-out;

    &:focus-visible {
      outline: none;
      filter: brightness(1.1) drop-shadow(0 0 5px rgba(0, 0, 0, 0.5));
    }

    @include hover {
      filter: brightness(1.1) drop-shadow(0 0 5px rgba(0, 0, 0, 0.5));
    }

    @include big-desktop {
      padding: 8px 14px;
      font-size: 16px;
    }
    @include small-tablet {
      left: 50%;
      padding: 7px 12px;
      font-size: 12px;
      line-height: 160%;
      transform: translate(-50%, -50%);
    }
  }
}

.sideform {

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include tablet {
      gap: 16px;
    }

    @include small-tablet {
      gap: 8px;
    }
  }

  & span {
    color: var(--light-color) !important;
  }
}
