.rating {

  &__container {
    display: flex;
    justify-content: space-around;
    gap: 80px;
    border: 3px solid var(--blue-color);
    border-radius: 26px;
    padding: 35px 40px;

    @include tablet {
      flex-direction: column;
      gap: 20px;
      padding: 20px;
    }
  }

  &__title {
    margin-bottom: 30px;
  }

  &__content {
    max-width: 55%;

    @include tablet {
      max-width: 100%;
      text-align: center;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    text-align: left;
    color: var(--dark-text-color);

    @include tablet {
      max-width: 100%;
      text-align: center;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    gap: 40px;
    padding-top: 20px;

    @include tablet {
      padding-top: 0;
      justify-content: center;
    }

    @include mobile {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__yandex {
  }

  &__google {
  }
}
