.partners {
  &__slider {

    .swiper-wrapper {
      display: flex;
      align-items: center;
      transition-timing-function : linear;
    }

  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  &__img {
    height: 80px;
    max-width: 100%;
    object-fit: contain;

    @include tablet {
      height: 70px;
    }

    @include mobile {
      height: 60px;
    }
    @include small-mobile {
      height: 50px;
    }
  }
}
